<!--资源详情-->
<template>
  <div class="res-detail">
    <!-- 视频部分 -->
    <div class="main-video">
      <div class="main-video-content">
        <!-- video -->
        <template v-if="resResource.resourceFileType === 9030">
          <div class="main-video-content-video">
            <div class="left">
              <div class="video-wrap">
                <video
                  controls
                  style="width:100%;height:100%;object-fit:cover;outline: none"
                  :src="resourceAuth.resourcePath"
                ></video>
                <!-- 需要输入密码 -->
                <div v-if="!resourceAuth.auth&&resourceAuth.authType===9072" class="need-pwd">
                  <div class="p-center">
                    <p>请输入密码观看</p>
                    <div class="pwd-input">
                      <el-input placeholder="请输入密码" v-model="pwd"></el-input>
                      <div @click="handleCheckPwd" class="arrow">
                        <i class="iconfont icon-arrow"></i>
                      </div>
                    </div>
                  </div>
                </div>
                <!-- 没有权限观看 -->
                <div v-else-if="!resourceAuth.auth&&resourceAuth.authType!==9072" class="no-auth">
                  <div class="p-center">
                    <i class="iconfont icon-lock-"></i>
                    <p>您没有权限查看此资源，再看看别的吧</p>
                  </div>
                </div>

              </div>
              <div class="video-control">
                <div>
                  <i class="iconfont icon-eyes"></i> {{resResource.visitCount}}
                </div>
                <div>
                  <i class="iconfont icon-arrow-1"></i> 下载
                </div>
                <!-- <div>
                  <i class="iconfont icon-heart"></i> 收藏
                </div> -->
                <div>
                  <i class="iconfont icon-share"></i> 分享
                </div>
                <div style="color:#FF8F1F">
                  <i style="color:#FF8F1F" class="iconfont icon-solid-star"></i> {{resResource.avgScore}}
                </div>
              </div>
            </div>
            <div class="right">
              <div class="title">标记</div>
              <el-scrollbar>
                <!-- 有知识点 -->
                <template v-if="resourceNoteList&&resourceNoteList.length>0">
                  <ul>
                    <li v-for="(item,i) in resourceNoteList" :key="i">
                      <div>{{item.resourceNoteContext}}</div>
                      <div>{{item.resourceNoteExplain}}</div>
                    </li>
                  </ul>
                </template>
                <!-- 没有知识点 -->
                <template v-else>
                  <div class="no-mark">
                    <i class="iconfont icon-placeholder-2"></i>
                    <p>看样子还没有相关标记</p>
                    <p>明天再来看看吧</p>
                  </div>
                </template>
              </el-scrollbar>
            </div>
          </div>
        </template>
        <!-- PDF -->
        <!-- <div class=""></div> -->
        <template v-if="resResource.resourceFileType  === 9031">
          <div class="main-video-content-pdf">
            <div class="pdf-content">
              <pdf :src="pdf.src"></pdf>
            </div>
            <div class="video-control">
              <div>
                <i class="iconfont icon-eyes"></i> {{resResource.visitCount}}
              </div>
              <div>
                <i class="iconfont icon-arrow-1"></i> 下载
              </div>
              <!-- <div>
                <i class="iconfont icon-heart"></i> 收藏
              </div> -->
              <div>
                <i class="iconfont icon-share"></i> 分享
              </div>
              <div style="color:#FF8F1F">
                <i style="color:#FF8F1F" class="iconfont icon-stars"></i> {{resResource.avgScore}}
              </div>
            </div>
          </div>
        </template>

        <div class="main-video-content-info">
          <div class="title">{{resResource.resourceName}}</div>
          <div class="info">
            <div>{{resResource.userName}}</div>
            <div>{{resResource.orgName}}</div>
            <div>{{resResource.createtime | formatTime}}</div>
            <div>{{resResource.subjectInfo}}</div>
          </div>
          <div :class="['desc',{'down':descShow === 'down'}]">
            {{resResource.resourceMask}}
            <i
              v-if="true"
              @click="handleToggleDesc"
              :class=" descShow === 'down' ? 'el-icon-arrow-down' : 'el-icon-arrow-up'"
            ></i>
          </div>
        </div>
      </div>
    </div>

    <div style="background:#f3f5f7;overflow:hidden">
      <!-- home work -->
      <div class="home-work">
        <div class="title">
          <!-- <div
            @click="handleToggleWorkType('homework')"
            class="project"
            :class="{active:workType==='homework'}"
          >作业10</div> -->
          <!-- <div
            @click="handleToggleWorkType('attach')"
            class="p-info"
            :class="{active:workType==='attach'}"
          >附件7</div> -->
          <div
            class="p-info active"
          >附件{{resAttachmentList.length}}</div>
          <div class="more">
            更多
            <i class="el-icon-arrow-down"></i>
          </div>
        </div>
        <!-- home
        <template v-if="workType === 'homework'">
          <div class="project-content">
            <div v-for="n in 10" :key="n" class="item">
              <div class="img-box"></div>
              <div class="name text-overflow">宋冠玲</div>
              <div class="text-overflow">成都七中（林荫校区）</div>
            </div>
          </div>
        </template>
        // 附件
        <template v-if="workType === 'attach'">
          <div class="attach-content">
            <div v-for="n in 10" :key="n" class="item">
              <div class="img-wrap"></div>
              <div class="desc">七年级上第4课《古代诗 歌四首》.word</div>
            </div>
          </div>
        </template> -->
        <template>
          <div class="attach-content">
            <div v-for="item in resAttachmentList" :key="item.attachmentId" class="item">
              <div class="img-wrap">
                <div class="p-center" :style="{backgroundPosition:formatTyle(item)}"></div>
              </div>
              <div class="desc">{{item.attachmentName}}</div>
            </div>
          </div>
        </template>
      </div>

      <!-- 评论 -->
      <div class="comments">
        <div class="comments-content">
          <div class="main">
            <div class="comments-box">
              <!-- <div class="rate">
                <span>资源评分:</span>
                <el-rate v-model="value1"></el-rate>
              </div> -->
              <div class="content">
                <el-input
                  :disabled="openComment==2"
                  :rows="2"
                  resize="none"
                  type="textarea"
                  placeholder="说点什么吧..."
                  v-model="commentValue"
                  maxlength="200"
                ></el-input>
                <div class="comments-control">
                  <span>{{commentValue.length}}/200</span>
                  <span @click="handlePubComment" :class="{canNotComment:openComment==2}" class="comments-btn">发布</span>
                </div>
              </div>
            </div>
            <!-- 开启评论 -->
            <template v-if="openComment==1">
              <div class="comments-list">
                <div class="title">
                  全部评论
                  <span>{{commentsTotal}}</span>
                </div>
                <resCommendBox @commentOver="commentOver"  :info="item" v-for="item in commentsList" :key="item.commentId"></resCommendBox>
              </div>
            </template>
            <!-- 关闭评论 -->
            <template v-if="openComment==2">
              <div class="close-commend">
                <i class="iconfont iconpinglun_kong"></i>
                <p>主人在神游，评论已关闭</p>
              </div>
            </template>
          </div>
          <!-- 更多评论 评论过多 且 没有关闭评论 -->
          <!-- <template v-if="openComment==1 && fomatCommentList.length<commentsTotal">
            <div class="more-commend">
              <span @click="commentsJudeg=commentsTotal">查看更多评论</span>
            </div>
          </template> -->
        </div>

        <div class="comments-info">
          <div class="comments-info-active">
            <div class="active-info p-center">
              <p class="name">教研活动</p>
              <p class="btn">发起调研</p>
            </div>
          </div>
          <div class="comments-res">
            <div class="title">相关资源</div>
            <ul class="content">
              <li v-for="item in relevantResourceList" :key="item.resourceId">
                <div class="img-wrap">
                  <img :src="item.resourcePic" alt="资源封面" />
                  <div class="time">{{item.resourceFileTime}}</div>
                </div>
                <p>{{item.resourceMask}}</p>
              </li>
            </ul>
          </div>
        </div>
      </div>

      <res-footer></res-footer>
    </div>
  </div>
</template>

<script>
// resourceFileType
//视频 :9030;
//文档 :9031;
//其他 :9032;
import {dateFormat} from './../../../assets/javascript/ultis'
import pdf from 'vue-pdf'
export default {
  props:['id'],
  components: {
    resFooter: () => import("@/components/res-footer"),
    resCommendBox: () => import("@/components/res-cmdBox"),
    pdf
  },
  data() {
    return {
      value1: 0,
      /* 课程描述 收起-展开 */
      descShow: "down",
      /* 作业 / 附件  homework attach */
      workType: "homework",
      /* start */
      commentsList:[],
      commentsTotal:0,
      commentsJudeg:20, //查看更多评论 临界点
      relevantResourceList:[],
      resAttachmentList:[],
      resResource:{},
      resourceAuth:{},
      resourceNoteList:[],
      openComment:0, // 1 true 2 false
      commentValue:'',
      pwd:'', //输入密码,
      pdf:{
        src:'./../../../../public/test.pdf'
      }
    };
  },
  created(){
    this.id && this.recordVisitDetail();
    this.id && this.initDetailInfo();
    // this.pdf.src = pdf.createLoadingTask(this.pdf.src)
  },
  filters:{
    formatTime(val){
      return dateFormat(new Date(val),'yyyy-MM-dd')
    }
  },
  computed:{
    // 更多评论
    fomatCommentList(){
      if(this.commentsTotal > this.commentsJudeg) {
        return this.commentsList.slice(0, this.commentsJudeg)
      } else {
        return this.commentsList
      }
    }
  },
  methods: {
    /*  */
    recordVisitDetail(){
      this.$api.recordVisitDetail(this.id)
    },
    /* 初始化 */
    initDetailInfo(){
      this.$api.resourceDetailPage(this.id).then(res => {
        console.log(res.data);
        const {commentsList,relevantResourceList,
        resourceNoteList,resourceAuth,
        resAttachmentList,resResource,openComment} = res.data;
        this.commentsList = commentsList; 
        this.relevantResourceList = relevantResourceList; 
        this.resourceNoteList = resourceNoteList; 
        this.resourceAuth = resourceAuth; 
        this.resAttachmentList = resAttachmentList; 
        this.resResource = resResource;
        this.openComment = openComment;
        this.commentsTotal = commentsList.length;
      })
    },
    /* 切换课程描述-展开/收起 */
    handleToggleDesc() {
      this.descShow = this.descShow === "down" ? "up" : "down";
    },
    // handleToggleWorkType(type) {
    //   this.workType = type;
    // },
    handleCheckPwd(){
      if(!this.pwd) return false;
      this.$api.verifyAuthPassword(this.id,this.pwd).then(res => {
        this.$set(this.resourceAuth, "resourcePath" ,res.data.resourcePath)
        this.resourceAuth.auth = res.data.auth;
      })
    },
    //评论发布
    handlePubComment(){
      let str = this.commentValue.replace(/^\s*|\s*$/g,"");
      if(!str) {
        return this.$myLayer.dangerLayer("请输入评论信息")
      }
      const params = {
        attributeObj:this.id,
        context:str
      }
      this.$api.addComment(params).then(res => {
        // TODO -> 刷新评论
        this.commentValue = '';
        this.commentOver();
      })
    },
    commentOver(){
      // TODO -> 刷新评论
      this.$api.getComment(this.id).then(res => {
        this.commentsList = [...res.data]; 
        this.$myLayer.successLayer("评论成功")
      })
    },
    formatTyle(item){
      const name = item.attachmentName;
      let index = name.lastIndexOf(".");
      let target = name.slice(index);
      switch (target) {
        case '.pdf':
          return '0 0';
        case '.word':
          return '0 -81px';
        case '.xls':
          return '0 -81px';
        case '.exe':
          return '0 -162px';
        case '.rar':
          return '0 -243px';
        case '.mp4':
          return '0 -405px';
        case '.jpg':
          return '0 -486px';
        case '.png':
          return '0 -486px';
        case '.jpeg':
          return '0 -486px';
        default:
          return '0 -324px';
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.res-detail {
  padding-top: 10px;
  .main-video {
    background: #fff;
    padding: 30px 0;
    .main-video-content {
      // width: 80%;
      // min-width: 1200px;
      width: 1200px;
      margin: 0 auto;
      &-video {
        display: flex;
        height: 650px;
        .left {
          flex: 1;
          .video-wrap {
            height: calc(100% - 65px);
            position: relative;
            .no-auth {
              position: absolute;
              left: 0;
              top: 0;
              bottom: 0;
              right: 0;
              background: #2b2b2b;
              z-index: 9999;
              text-align: center;
              i {font-size: 60px;}
              p{font-size: 16px}
            }
            .need-pwd {
              position: absolute;
              left: 0;
              top: 0;
              bottom: 0;
              right: 0;
              background: #2b2b2b;
              z-index: 9999;
              text-align: center;
              &>div.p-center {
                width: 250px;
                p {font-size: 16px;}
              }
              .pwd-input {
                position: relative;
                margin-top: 20px;
                .arrow {
                  cursor: pointer;
                  position: absolute;
                  right: 1px;
                  top: 1px;
                  height: 34px;
                  width: 46px;
                  text-align: center;
                  line-height: 34px;
                  background: #2b2b2b;
                  border-top-right-radius: 4px;
                  border-bottom-right-radius: 4px;
                  border-left: 1px solid #666;
                  &:hover {
                    background: #2695ff;
                    i {
                      color: #fff
                    }
                  }
                }
                i {
                  font-size: 20px;
                  display: inline-block;
                  transform: rotate(90deg);
                  color: #666;
                }
              }
            }
          }
          .video-control {
            height: 65px;
            background: #202020;
            display: flex;
            align-items: center;
            & > div {
              margin-left: 30px;
            }
          }
        }
        .right {
          width: 320px;
          height: 650px;
          background: #141414;
          .title {
            height: 45px;
            line-height: 45px;
            padding-left: 15px;
            border-bottom: 1px solid #323232;
            color: #c7c7c7;
          }
          ul {
            padding: 15px;
            max-height: 700px;
            li {
              border-bottom: 1px solid #323232;
              padding: 10px 0;
              color: #c7c7c7;
              cursor: pointer;
            }
          }
          .no-mark {
            margin-top: 40px;
            text-align: center;
            i {
              font-size: 60px;
            }
          }
        }
      }

      &-pdf {
        height: 650px;
        // background: #f3f5f7;
        .pdf-content {
          height: 585px;
        }
        .video-control {
          height: 65px;
          display: flex;
          align-items: center;
          background: #f2f2f2;
          & > div {
            margin-left: 30px;
          }
        }
      }

      &-info {
        .title {
          font-size: 20px;
          margin: 23px 0 12px 0;
        }
        .info {
          display: flex;
          margin-bottom: 16px;
          & > div {
            line-height: 1;
            margin-right: 15px;
            padding-right: 15px;
            border-right: 1px solid #ccc;
            &:last-child {
              border-right: none;
            }
          }
        }
        .desc {
          position: relative;
          max-width: 880px;
          text-align: justify;
          color: #909399;
          overflow: hidden;
          padding-right: 30px;
          &.down {
            height: 20px;
          }
          i {
            position: absolute;
            right: 0;
            top: -1px;
            font-size: 20px;
            cursor: pointer;
          }
        }
      }
    }
  }
}

/* home work */
.home-work {
  // width: 80%;
  // min-width: 1200px;
  width: 1200px;
  background: #fff;
  margin: 20px auto 0;
  .title {
    display: flex;
    padding: 15px 20px 0;
    & > div {
      color: #303133;
      font-size: 16px;
      padding-bottom: 5px;
      cursor: pointer;
    }
    // .p-info {
    //   margin-left: 30px;
    // }
    .more {
      flex: 1;
      text-align: right;
    }
    .project {
    }
    .active {
      color: #2695ff;
      border-bottom: 2px solid #2695ff;
    }
  }

  .project-content {
    padding: 15px 0 20px;
    display: flex;
    flex-wrap: wrap;
    .item {
      width: 12.5%;
      padding: 0 20px;
      margin-bottom: 20px;
      .img-box {
        width: 100%;
        height: 0;
        padding-bottom: 100%;
        background: url(./../../../assets/image/school.jpg) no-repeat center
          center;
        background-size: 80% 80%;
      }
      div {
        color: #606266;
        &.name {
          margin-top: 12px;
        }
      }
    }
  }
  .attach-content {
    padding: 15px 0 20px;
    display: flex;
    flex-wrap: wrap;
    .item {
      width: 12.5%;
      padding: 0 20px;
      text-align: center;
      margin-bottom: 20px;
      .img-wrap {
        width: 100%;
        height: 0;
        padding-bottom: 100%;
        border: 1px solid #d7d7d7;
        position: relative;
        border-radius: 6px;
        div {
          width: 67px;
	        height: 80px;
          background: url(./../../../assets/image/fileType.png) no-repeat;
          background-size: 68px 566px;
        }
      }
      .desc {
        margin-top: 10px;
        text-align: left;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }
    }
  }
}

.comments {
  // width: 80%;
  // min-width: 1200px;
  width: 1200px;
  margin: 20px auto 65px;
  display: flex;
  .comments-content {
    flex: 1;
    .main {
      padding: 30px;
      background: #fff;
    }
    .comments-box {
      .rate {
        display: flex;
        align-items: center;
        & > span {
          margin-right: 10px;
        }
      }
      .content {
        margin-top: 20px;
        .comments-control {
          text-align: right;
          margin: 16px 0 25px;
          span {
            color: #c1c4cc;
          }
          .comments-btn {
            display: inline-block;
            width: 128px;
            height: 36px;
            border-radius: 18px;
            background:#2695ff;
            color: #fff;
            line-height: 36px;
            text-align: center;
            cursor: pointer;
            margin-left: 20px;
            &.canNotComment {
              cursor: no-drop;
              background: #cacbcc;
            }
          }
        }
      }
    }
    .comments-list {
      border-top: 1px solid #e0e4eb;
      margin-top: 30px;
      padding-top: 30px;
      .title {
        font-size: 16px;
        span {
          color: #909399;
        }
      }
    }
    .close-commend {
      text-align: center;
      margin: 40px 0;
      p {
        color: #909399;
        line-height: 24px;
      }
      i {
        font-size: 60px;
      }
    }
    .more-commend {
      height: 70px;
      background: #fff;
      line-height: 70px;
      text-align: center;
      color: #606266;
      border-top: 1px solid rgba(224, 228, 235, 0.5);
    }
  }
  .comments-info {
    width: 300px;
    margin-left: 24px;
    .comments-info-active {
      position: relative;
      height: 140px;
      background: #fff;
      margin-bottom: 24px;
      .active-info {
        text-align: center;
        p.name {
          color: #606266;
          font-size: 16px;
        }
        p.btn {
          width: 120px;
          height: 36px;
          background: rgba(38, 149, 255, 1);
          color: #fff;
          border-radius: 4px;
          margin-top: 15px;
          line-height: 36px;
          padding: 0;
        }
      }
    }
    .comments-res {
      padding: 20px;
      background: #fff;
      .title {
        color: #303133;
        font-size: 16px;
      }
      .content {
        li {
          cursor: pointer;
          margin-top: 20px;
          height: 70px;
          .img-wrap {
            position: relative;
            display: inline-block;
            width: 124px;
            height: 70px;
            overflow: hidden;
            border-radius: 6px;
            margin-right: 10px;
            img {
              width: 100%;
              height: 100%;
            }
            .time {
              position: absolute;
              right: 5px;
              bottom: 5px;
              padding: 0 5px;
              background: rgba(0, 0, 0, 0.2);
